
*,*:before,*:after{
    margin:0; 
    padding:0; 
    box-sizing:border-box;
}
   
html{
    box-sizing:inherit
}

body{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   
}

main.container{
    width: 100%;
    @include flexCentered;
    flex-direction: column;
}

ul>li{
    list-style-type: none;
}


section.ui-container{
    @include borderTest(black);

}

